import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {ConfigService} from './config.service';
import {Router} from '@angular/router';

@Injectable()
export class CallApiService {
  url: string;

  constructor(private http: HttpClient, private config: ConfigService, private router: Router) {
    }

  signUp(obj): Observable<any> {
    return this.http.post<any>(this.config.baseURL() + 'register', obj).pipe(map(response => {
      return response;
    }, err => {
      throw err;
    }));
  }
  login(obj): Observable<any> {
    return this.http.post<any>(this.config.baseURL() + 'login', obj).pipe(map(response => {
      return response;
    }, err => {
      throw err;
    }));
  }
  logout() {
    localStorage.clear();
    this.router.navigate(['/home']);
  }
  getKeys(): Observable<any> {
    return this.http.get<any>('./assets/keys.json').pipe(map(res => {
      return res;
    }));
  }
  getWords(): Observable<any> {
    return this.http.get<any>('./assets/words.json').pipe(map(res => {
      return res;
    }));
  }
  getScoreBoard(): Observable<any> {
    return this.http.get<any>(this.config.baseURL() + 'User/getAllScors').pipe(map(res => {
      return res;
    }));
  }
  getMyWords(id): Observable<any> {
    return this.http.get<any>(this.config.baseURL() + 'User/getMyWord/' + id).pipe(map(res => {
      return res;
    }, err => {
      throw err;
    }));
  }
  getAllWords(): Observable<any> {
    return this.http.get<any>(this.config.baseURL() + 'User/1111111').pipe(map(res => {
      return res;
    }, err => {
      throw err;
    }));
  }
  setWord(obj): Observable<any> {
    return this.http.post<any>(this.config.baseURL() + 'User/setWord', obj).pipe(map(response => {
      return response;
    }, err => {
      throw err;
    }));
  }
  setScore(obj): Observable<any> {
    return this.http.post<any>(this.config.baseURL() + 'User/setScor', obj).pipe(map(response => {
      return response;
    }, err => {
      throw err;
    }));
  }
}
