import {Component, OnInit} from '@angular/core';
import {CallApiService} from '../shared/call-api.service';
import {Router} from '@angular/router';

declare let $: any;

@Component({
  selector: 'app-right-result',
  templateUrl: './right-result.component.html',
  styleUrls: ['./right-result.component.css']
})
export class RightResultComponent implements OnInit {
  rows = [{}, {}, {}, {}, {}, {}, {}, {}, {}];
  cols = [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}];
  public keys: any[] = [];
  collectedWordArray: any[] = [];
  collectedWord: string;
  words: any[] = [];
  myWords: any[] = [];
  showSuccess = false;
  user_id: any;
  scoreBoard: any[] = [];
  userScore: any;
  timer: { mins: any, secs: any } = {
    mins: '01',
    secs: '00'
  }

  constructor(private apiService: CallApiService) {
  }

  ngOnInit() {
    this.user_id = localStorage.getItem('user_id');
    this.toggleModal();
    this.initKeys();
    this.initWords();
    this.initMyWords();
    this.initScoreBoard();
  }

  initScoreBoard() {
    this.apiService.getScoreBoard().subscribe(res => {
      if (res.status == 200) {
        this.scoreBoard = res.data;
        this.scoreBoard.forEach(element => {
          if (element.user_id == this.user_id) {
            this.userScore = element.scor;
          }
        });
      }
    }, error => {
      console.log(error.error);
    });
  }

  initKeys() {
    this.apiService.getKeys().subscribe(res => {
      this.keys = res.data;
    });
  }

  initWords() {
    this.apiService.getWords().subscribe(res => {
      this.words = res.data;
    });
  }

  initMyWords() {
    this.apiService.getMyWords(this.user_id).subscribe(res => {
      this.myWords = res.data;
    }, error => {
      console.log(error.error);
    });
  }

  toggleModal(event?) {
    $('#myModal').modal('toggle');
    $('.modal-backdrop').remove();
    if (event) {
      this.startTimer();
    }
  }

  startTimer() {
    this.timer.mins = '00';
    this.timer.secs = '59';
    let t = setInterval(() => {
      this.timer.secs = (+this.timer.secs - 1).toString();
      if (+this.timer.secs == 0) {
        clearInterval(t);
        $('#myModal').modal('hide');
        $('#myModal2').modal('hide');
        this.apiService.logout();
      }
    }, 1000);
  }

  logout() {
    this.apiService.logout();
  }

  getKey(r, c) {
    if (this.keys.length > 0) {
      let key = this.keys.filter(obj => obj.x == r && obj.y == c);
      return key[0].char;
    }
  }

  getClass(r, c) {
    if (this.keys.length > 0) {
      let key = this.keys.filter(obj => obj.x == r && obj.y == c);
      return key[0].class;
    }
  }

  keyClicked(r, c) {
    this.showSuccess = false;
    this.collectedWord = '';
    const key = this.keys.filter(obj => obj.x == r && obj.y == c);
    if (key[0].class != 'disabled') {
      this.collectedWordArray.push({...key[0], status: false});
    }
    this.collectedWordArray.forEach(element => {
      this.collectedWord += element.char;
    });
    this.words.forEach(word => {
      if (word.toLowerCase().search(this.collectedWord) != -1) {
        this.collectedWordArray[this.collectedWordArray.length - 1].status = true;
      }
    });
    this.words.forEach(word => {
      if (word.toLowerCase() == this.collectedWord) {
        this.showSuccess = true;
        this.collectedWordArray = [];
        const user_id = localStorage.getItem('user_id');
        const obj = {
          user_id: user_id,
          word: this.collectedWord
        };
        this.apiService.setWord(obj).subscribe(res => {
          if (res.status == 200) {
            this.initMyWords();
          }
        });
        const myWordsFilter = this.myWords.filter(obj => obj.word.toLowerCase() == this.collectedWord);
        if (myWordsFilter.length == 0) {
          const obj = {
            user_id: user_id,
            scor: (+this.userScore) + (+this.collectedWord.length)
          };
          console.log('score', obj.scor);
          this.apiService.setScore(obj).subscribe(res => {
            this.initScoreBoard();
          });
        }
      }
    });
  }

  clearKey() {
    this.collectedWordArray.splice(-1, 1);
  }
}
