import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';


import {AppComponent} from './app.component';
import {HomeComponent} from './home/home.component';
import {RightResultComponent} from './right-result/right-result.component';
import {ScoreboardComponent} from './scoreboard/scoreboard.component';
import {CallApiService} from './shared/call-api.service';
import {HttpClientModule} from '@angular/common/http';
import {ConfigService} from './shared/config.service';
import { MyWordsComponent } from './my-words/my-words.component';
import {AuthGuardService} from './shared/auth-guard.service';


const appRoutes: Routes = [
  {path: '', redirectTo: 'home', pathMatch: 'full'},
  {path: 'home', component: HomeComponent},
  {path: 'scoreboard', component: ScoreboardComponent, canActivate: [AuthGuardService]},
  {path: 'result', component: RightResultComponent, canActivate: [AuthGuardService]},
];

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    RightResultComponent,
    ScoreboardComponent,
    MyWordsComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes)

  ],
  providers: [CallApiService, ConfigService, AuthGuardService],
  bootstrap: [AppComponent]
})
export class AppModule {
}
